@import "@/assets/scss/main.scss";

.reply__card__wrapper {
  .reply__card {  
    .reply__card__content {
      border-top: 1px solid #e6ecf0;
      padding: 16px 24px;
      .reply__card__title {
        display: flex;
        align-items: top;
        .reply__card__avatar {
          width: 50px;
          height: 50px;
          margin-top: 0;
          border-radius: 50%;
        }
        .reply__card__info {
          display: flex;
          align-items: top; 
          margin: 2px 0 0 10px;
          .reply__card__name {
            color: $scale-gray10;
            font-weight: 700;
            font-size: 16px;
            margin-right: 8px;
          }
          .reply__card__account,
          .reply__card__time {
            color: $secondary-gray;
            font-weight: 400;
            font-size: 14px;
            margin: 2px 0 0 0;
          }
        }
        .reply__card__target {
          display: flex;
          color: $secondary-gray;
          font-weight: 400;
          font-size: 14px;
          margin: 8px 0 8px 10px;
          P {
            margin-right: 2px;
          }
          .reply__card__target__account {
            color: $brand-orange;
          }
        } 
      }
      .reply__card__description {
        color: $scale-gray10;
        font-weight: 400;
        font-size: 16px;
        margin: 0 0 8px 60px;
      }
    }
  }
}
