@import "@/assets/scss/main.scss";

.reply__container {
  position: relative;
  margin: auto 9.3vw auto 9.3vw;
}
.reply {
  position: absolute;
  left: 14.4vw;
  right: 21.2vw;
  border: 1px solid #e6ecf0;
  .reply__header {
    display: flex;
    align-items: center;
    border-bottom: 1px solid #e6ecf0;
    padding: 24px 28px;
    img {
      width: 17px;
      height: 14px;
      margin-right: 20px;
    }
  }
  .tweet__content {
    padding: 16px;
    .tweet__title {
      display: flex;
      .tweet__avatar {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        margin-right: 8px;
      }
      .tweet__name {
        color: $scale-gray10;
        font-weight: 700;
        font-size: 16px;
      }
      .tweet__account {
        color: $secondary-gray;
        font-weight: 400;
        font-size: 14px;
      }
    }
    .tweet__description {
      color: $scale-gray10;
      font-weight: 400;
      font-size: 24px;
      margin: 8px 0;
    }
    .tweet__time {
      color: $secondary-gray;
      font-weight: 500;
      font-size: 14px;
    }
    .tweet__count {
      display: flex;
      line-height: 23px;
      color: $secondary-gray;
      font-weight: 500;
      font-size: 19px;
      .tweet__num {
        color: $scale-gray10;
        font-weight: 700;
        font-family: Montserrat;
        margin-right: 3px;
        line-height: 23px;
      }
      .tweet__reply {
        margin-right: 24px;
      }
    }
    .tweet__card__footer {
      display: flex;
      img {
        width: 25px;
        height: 23px;
        margin-right: 134px;
        cursor: pointer;
      }
    }
    hr {
      color: #e6ecf0;
      width: 99%;
    }
  }
}
